@import url(https://fonts.googleapis.com/css2?family=Homemade+Apple&family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dawning+of+a+New+Day&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;min-height:100vh;overflow:hidden;display:block;position:relative;color:#bdb1a2}
.title-text{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);color:#fff;font-family:"Homemade Apple";font-size:5vw}.Landing{height:100vh;background-image:url(/static/media/landing.a4ddb6a6.jpg);background-size:cover;background-repeat:no-repeat;background-position:50% 50%;text-align:center;position:relative}
.welcome-bgrnd{position:relative;min-height:100vh;display:table;background-color:#bdb1a2;text-align:center;overflow:hidden}.welcome-msg{display:table-cell;vertical-align:middle;padding:10vw;color:#26201d}.welcome-txt{font-size:2.5vh}.welcome-spacing{height:5vh}
@font-face{font-family:Hello-Paris;src:url("/static/media/Hello Paris Sans Regular.18f25d4b.ttf") format("truetype");font-weight:normal;font-style:normal}body{font-family:Lato,Tahoma,serif}.hdr-msg{font-family:Hello-Paris;font-size:5em}.about-bgrnd{position:relative;min-height:100vh;display:table;background-color:#5f5d4b;text-align:center;overflow:hidden}.about-page{display:table-cell;vertical-align:middle;padding:30px;color:#f1efe8;font-family:"Lato"}.about-msg{padding-top:30px;padding-bottom:30px}.about-txt{font-size:2vh}.about-spacing{height:5vh;width:5vw}.sbs-image{padding:20px;max-height:70vh;max-width:70vw;object-fit:cover}
.footer-container{position:relative;bottom:0;width:100%;padding:10px 0 0px 0;background-color:#bdb1a2}.copy-txt{color:#fff}
.accomodations-bgrnd{position:relative;min-height:100vh;display:table;background-color:#f1efe8;text-align:center;overflow:hidden}.accomodations-msg{display:table-cell;vertical-align:middle;padding:10vw;color:#26201d}.accomodations-txt{font-size:2.5vh;padding-bottom:5vh}.accomodations-spacing{height:5vh}
