@import './base/variables';

.accomodations-bgrnd {
    position: relative;
    min-height: 100vh;
    display: table;
    background-color: $off-white;
    text-align: center;
    overflow: hidden;
}

.accomodations-msg {
    display: table-cell;
    vertical-align: middle;
    padding: 10vw;
    color: $dark-grey;
}

.accomodations-txt {
    font-size: 2.5vh;
    padding-bottom: 5vh;
}

.accomodations-spacing {
    height: 5vh;
}